	module.exports = {"data":{"markdownRemark":{"html":"<h2>Customer Journey Map</h2>\n<p>A customer journey map explains the potential trayectory of the customer’s experience: from initial contact, through the process of engagement and into a long-term relationship.\nIt may focus on a particular part of the trayectory or give an overview of the entire experience. What it always does is identify key interactions that the customer has with the organization. It talks about the user’s feelings, motivations and questions for each of these touchpoints.\nIt often provides a sense of the customer’s greater motivation. What do they wish to achieve, and what are their expectations of the organization?\nA customer journey map takes many forms but typically appears as some type of infographics. Whatever its form, the goal is the same: to teach organizations more about their customers.</p>\n<p><img src=\"https://www.smashingmagazine.com/wp-content/uploads/2014/12/1-broadband-provider-journey-opt.jpg\" alt=\"A customer journey map takes many forms but typically appears as an infographic.\"></p>\n<h4>More Information:</h4>\n<p>All about customer journey map here: <a href=\"https://www.smashingmagazine.com/2015/01/all-about-customer-journey-mapping/\">https://www.smashingmagazine.com/2015/01/all-about-customer-journey-mapping/</a></p>","fields":{"slug":"/user-experience-design/customer-journey-map/"},"frontmatter":{"title":"Customer Journey Map"}}},"pathContext":{"slug":"/user-experience-design/customer-journey-map/"}}

